import style from "./header.module.css"
import { Link } from 'react-router-dom'

const Header = (props) =>{
    return(
        <header>
            <div className="container">
                <div className="row">
                    <div className="col-sm-12 col-md-4 col-lg-3">
                    <Link to='project-cards/'>
                            <h1 className={style.logo}>NVIDIA WORLD</h1>
                    </Link>
                    </div>
                    <div className="col-sm-5 col-md-3 col-lg-2 offset-sm-1 offset-md-1 offset-lg-3">
                        <Link to='project-cards/favorites'>
                            <p className={style.navItem}>Избранное </p>
                        </Link>
                    </div>
                
                    <div className="col-sm-5 col-md-3 col-lg-2 offset-sm-1 offset-md-1">
                        <a href className={style.navItem} onClick={props.openCart}>Корзина</a>
                    </div>
                </div>
            </div>
        </header>
    );
}

export default Header;