import style from "./banner.module.css"
const Banner = () => {
    return(
        <div className={"container" +' '+style.bannerSection}>
            <div className={"row"+' '+style.banner}>
                <div className={['col-md-5']+' '+ ['col-lg-5']+' '+['col-xl-5']+' '+style.bannerInner}>
                        <p className={style.textBanner}>
                        Здесь самые дешевые
                            <br /> 
                            <span>видеокарты</span>
                            <br />
                            <button className={style.bannerBtn}>
                                Выбрать модель
                            </button>
                        </p>
                </div>
            </div>
        </div>
    )
};

export default Banner;