import Card from "./card/Card";
import style from "./products.module.css";
import axios from "axios"
import React from "react";
import { AppContext } from "../../App";

const Products = (props) => {
    const context = React.useContext(AppContext);

    const onClickPlus = (obj) => {
        const findCartItem = context.cartItems.find((item) => item.uid === obj.uid)

        if (findCartItem) 
        { 
            axios.delete(`https://635e30cf03d2d4d47ae97533.mockapi.io/cart/${findCartItem.id}`)
            context.setCartItems(prev => prev.filter((items) => items.uid !== obj.uid));
        } else
        {
            
            axios.post("https://635e30cf03d2d4d47ae97533.mockapi.io/cart", obj)
            context.setCartItems([...context.cartItems, obj])
        }   
    }

    const onClickFav = (obj) => {
        const findFavoriteItem = context.favoriteItems.find(item => item.uid === obj.uid)

        if (findFavoriteItem)
        {
            axios.delete(`https://635e30cf03d2d4d47ae97533.mockapi.io/favorites/${findFavoriteItem.id}`)
            context.setFavoriteItems(prev => prev.filter((item) => item.uid !== obj.uid));
        } else
        {
            axios.post("https://635e30cf03d2d4d47ae97533.mockapi.io/favorites", obj)
            context.setFavoriteItems([...context.favoriteItems, obj])
        }   
    }

    const onSearchInput =(input) => {
        props.setSearch(input.target.value);
    }

    return(
        <div className="container">
            <div className={style.search}>
                <h2>Все видеокарты</h2>
                <div className={style.searchBlock}>
                    <img src="./img/search.png" alt="search" />
                    <input 
                        onChange={onSearchInput} 
                        placeholder="Search" 
                    />
                </div>
            </div>
            <div className={"row"+' '+style.cards}>
                {
                    context.products.filter(obj => obj.title.toLowerCase()
                                                            .includes(props.search.toLowerCase()))
                                    .map(obj => {return(  
                                        <div className="col-8 col-sm-6 col-md-5 col-lg-4 col-xl-3">
                                            <Card
                                                {...obj} 
                                                onClickPlus = {(obj) => {onClickPlus(obj)}}
                                                onClickFav = {(obj) => {onClickFav(obj)}}
                                            />
                                        </div>
                                    )})
                }
            </div>
        </div>
    )
};

export default Products;