import CartItem from "./cart-item/CartItem";
import style from "./cart.module.css";
import React from "react";
import { AppContext } from "../../App";
import {motion} from "framer-motion"


const Cart = (props) =>{
    const context = React.useContext(AppContext);
    const TotalSum = context.cartItems.reduce((prevSum, obj) => prevSum + obj.price, 0);
    
    return(
        <div className={style.overlay}>
                <div className={"container"+' '+style.cart}>
                        <div className={"row"+' '+style.titleBlock}>
                            <div className="col-md-8 col-sm-6 col-6">
                                <h2>Корзина</h2>
                            </div>

                            <div className={"col-md-4 col-sm-6 col-6"+' '+style.btnBlock}>
                                
                                <motion.button
                                    whileHover={{ scale: 1.1 }}
                                    whileTap={{ scale: 0.9 }}  
                                    className={style.closeBtn} 
                                    onClick={props.closeCart}>X
                                </motion.button>
                            </div>
                        </div>

                        <div className={"col-md-12"+' '+style.cartList}>
                            {
                                context.cartItems.map(obj => {return(
                                        <CartItem 
                                            {...obj}
                                            onRemoveCartItem={props.onRemoveCartItem}
                                        />                             
                                    ) } )
                            }
                        </div>

                        <div className={"col-md-12"+' '+style.totalPrice}>
                            <p className={style.totalPriceText}>Итог</p>
                            <font className={style.totalPriceSum}>{TotalSum} руб</font>
                            <button>Заказать</button>
                        </div>
                </div>
            </div>
    )
}

export default Cart;