import React from "react";
import style from "./favoriteCard.module.css";
import { AppContext } from "../../../App";
import {motion} from "framer-motion";


const FavoriteCard = (props) =>{
    const context = React.useContext(AppContext);

    const onClickPlus = () =>{
        let title = props.title;
        let price = props.price;
        let img = props.img;
        let id = props.id
        let uid = props.uid
        props.onClickPlus({id, uid, title, price, img})     
    }

    const onClickFav = () =>{
        let title = props.title;
        let price = props.price;
        let img = props.img;
        let id = props.id
        let uid = props.uid
        props.onClickFav({id, uid, title, price, img})
    }
    return(
        <div className={"container"+' '+style.card}>
            <div className="row">
                <div className="col-md-8">
                    <button 
                        className={style.checkFavorBtn} 
                        onClick={onClickFav}>
                           В избранном 
                    </button>
                </div>
                <div className={['col-md-12']+' '+style.imgDiv}>
                    <img className={style.cardImg} src={props.img} alt=""/>
                </div>
                <div className="col-md-12">
                    <span>{props.title}</span>
                </div>
                <div className={['col-md-12']+' '+style.decription}>
                    <p>{props.description}</p>
                </div>
                <div className="col-md-12">
                    <p className={style.label}>Цена</p>
                </div>
                <div className="col-md-12">
                    <div className={"row"+' '+style.priceSection}>
                        <div className="col-8 col-sm-9 col-md-8 col-lg-8">
                            <p className={style.price}>{props.price} 
                                <span> ₽</span>
                            </p>
                        </div>
                        <div className="col-4 col-sm-3 col-md-4 col-lg-4">
                            <motion.button
                                whileHover={{ scale: 1.1 }}
                                whileTap={{ scale: 0.9 }}  
                                className={context.itemAdded(props.uid) ? style.addBtn : style.cardBtn} 
                                onClick={onClickPlus}>
                                    <img src={context.itemAdded(props.uid) ? "./img/check.png" : "./img/icons8-plus-24.png"} alt=""/>
                            </motion.button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default FavoriteCard;