import "./App.css";
import React from "react";
import axios from "axios";
import {Route, Routes} from "react-router-dom"
import Header from "./components/header/Header";
import Footer from "./components/footer/footer";
import Cart from "./components/cart/Cart";
import Favorite from "./components/favorites/favorite";
import Home from "./components/home";

export const AppContext = React.createContext({});
function App(){
    

    const [cartOpened, setCartOpened] = React.useState(false);
    const [products, setProducts] = React.useState([]);
    const [cartItems, setCartItems] = React.useState([]);
    const [favoriteItems, setFavoriteItems] = React.useState([]);
    const [search, setSearch] =React.useState('');

    React.useEffect(() =>{
            async function axiosData(){
                const productData = await axios.get("https://635e30cf03d2d4d47ae97533.mockapi.io/products");
                const cartData = await axios.get("https://635e30cf03d2d4d47ae97533.mockapi.io/cart");
                const favoriteData = await axios.get("https://635e30cf03d2d4d47ae97533.mockapi.io/favorites");

                setProducts(productData.data);
                setCartItems(cartData.data);
                setFavoriteItems(favoriteData.data);
            }

           axiosData();
        },
        []
    )

    const onRemoveCartItem = (id) => {
        axios.delete(`https://635e30cf03d2d4d47ae97533.mockapi.io/cart/${id}`);
        setCartItems((prev) => prev.filter(item => Number(item.id) !== Number(id)));
    }

    const itemAdded = (uid) => {
        return cartItems.some((objCart) => objCart.uid === uid);
    }
    
    const itemFavorited = (uid) => {
        return favoriteItems.some((objFavorite) => objFavorite.uid === uid);
    }

    return(
        <AppContext.Provider value={{
            products,
            cartItems,
            favoriteItems,
            setProducts,
            setCartItems,
            setFavoriteItems,
            itemAdded,
            itemFavorited
        }}>
            <div className="app">
                {
                    cartOpened ? 
                    <Cart 
                        closeCart={() => setCartOpened(false)}
                        onRemoveCartItem={onRemoveCartItem}
                    /> : 
                    null
                }
                
                <Header 
                    title="title" 
                    openCart={() => setCartOpened(true)}
                />

                <Routes >
                    <Route path='project-cards/favorites'
                        element={
                            <Favorite />
                        }
                    />

                    <Route path='project-cards/'
                        element={
                            <Home
                                setSearch={setSearch} 
                                search={search}
                            /> 
                        }
                    />    
                </Routes> 

                <Footer />
            </div>
        </AppContext.Provider>
        
    );
}
export default App;